<script>
// import './TweenLite.min.js'
// import './EasePack.min.js'
// import './main.js'
import { anima } from "../../../util";
import { useI18n } from "../../../i18nPlugin";
export default {
  name: "Home",
  setup() {
    const i18n = useI18n();
    return {
      i18n,
    };
  },
  data() {
    return {
      mobileBg: this.$store.state.viewListen,
    };
  },
  mounted() {
    window.addEventListener("scroll", anima); //监听滚动触发anima函数
  },
  beforeRouteLeave(to, form, next) {
    // 离开路由移除滚动事件
    window.removeEventListener("scroll", anima);
    next();
  },
  methods: {
    goReception() {
      window.localStorage.setItem("path", "/reception");
      this.$router.push("/reception");
    },
    goAppStore() {
      window.location.href = "https://apps.apple.com/cn/app/qbay/id1540223620";
    },
    goAppGooglePlay() {
      window.location.href = "https://play.google.com/store/apps/details?id=com.qbaytek.customer";
    },
  },
};
